
<template>
  <div>
    <a-modal
      @cancel="handleCancel"
      @ok="handleCreate"
      :visible="visible"
      :confirmLoading="confirmLoading"
      title=""
      okText=""
    >
      <a-form :form="form">
        <a-form-item :labelCol="{ span: 8 }" :wrapperCol="{ span: 14 }" label="">
          <a-input
            v-decorator="[
              'name',
              {
                initialValue: currRecord.name,
                rules: [
                  { required: true, message: '' },
                  rulesObj.editdName,
                ]
              }
            ]"
            placeholder=""
          />
        </a-form-item>
        <a-form-item :labelCol="{ span: 8 }" :wrapperCol="{ span: 14 }" label="">
          <a-textarea
            placeholder=""
            v-decorator="[
              'description',
              {
                initialValue: currRecord.description && currRecord.description.replace(/^$/, ''),
                rules: [
                  rulesObj.desc,
                ]
              }
            ]"
          />
        </a-form-item>
        <a-form-item :labelCol="{ span: 8 }" :wrapperCol="{ span: 14,offset:8 }" label>
          <a-checkbox
            v-decorator="[
              'bootable',
              {
                initialValue: currRecord.bootable,
                valuePropName: 'checked',
              }
            ]"
          >
            
            <a-tooltip>
              <template slot="title"></template>
              <a-icon type="info-circle" />
            </a-tooltip>
          </a-checkbox>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>
<script>
import { baseModalMixins, formModalMixins } from "@/mixins/modalMixin";
import rulesObj from '@/utils/rules'
import { editDisk as fetchAPI  } from "@/api/store/disk";
export default {
  mixins: [baseModalMixins, formModalMixins],
  data() {
    return {
      fetchAPI,
      rulesObj,
      name: "edit"
    };
  },

  methods: {
    onShow () {
      this.formValues = { hardDiskId: this.currRecord.id }
    },
    handleCreate() {
      const self = this;
      this.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          Object.assign(self.formValues, values);
          self.handleFetch();
        }
      });
    },
  }
};
</script>
